.eone{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 3150px;
    background: #E7A908;
}
h1 {margin: 0% 2% 0% 2%;
    width: 20%;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 150px;
    line-height: 74px;
    color: #FFFFFF;
}
.arrow{
    margin: 0% 0% 0% 0%;
    height: 2px;
    width: 5px;
}
.box{
    height: 70px;
    width: 3%;
}
.imgg {
    margin: 0;
    width: 73%;
    height: 147px;
}
.txt {
     padding: 36px 0px 0px 0px;
    height: 4%;
    width: 70%;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 46px;
    color: #FFFFFF;
    margin: 1% 0% 1% 6%;
}
.eventmobile{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.eventcontainer{
    height: 87%;
}

.box1{
    display: flex;
    flex-wrap: wrap;
    height: 15%;
    width: 92%;
    margin: 3% 4% 2% 4%;
    background-color: #FFFFFF;
    border-radius: 10px;
}
.side1{
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    width: 54%;
}
.num{
    z-index: 1;
    margin: 4%;
    padding: 8% 3%;
    font-size: 176px;
    height: 10%;
    width: 40%;
    color: #F8E5B5;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
}
h2{ z-index: 2;
    margin: 0% 0% 0% -38%;
    height: 34%;
    font-family: Montserrat;
    font-weight: bold;
    font-style: normal;
    font-size: 44px;
    padding-top: 15%;
    width: 83%;
}
.pd {
    height: 57%;
    width: 20%;
    padding: 7% 6% 7% 14%;
}
.pd1 {
    height: 57%;
    width: 16%;
    padding: 7% 21% 7% 9%;
}
.arrow {
    width: 20%;
    height: 15px;
}
.btnn {
    margin-right: 30px;
    color: white;
    font-size: 24px;
}
.btn1 {
    text-decoration: none;
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    background-color: #E7A908;
    height: 8%;
    margin: -4% 6% 0% 6%;
    border-radius: 8px;
    padding: 2% 3%;
    width: 30%;
    font-size: 20px;
}
.btn2 {
        line-height: 35px;
    color: white;
    background-color: black;
    height: 8%;
    margin: -4% 6% 0% 6%;
    border-radius: 8px;
    padding: 2% 7%;
    width: 20%;
    font-size: 22px;
    text-align: center;
    vertical-align: middle;
}

.btn2:hover{
    cursor: pointer;
}

.btn1:hover{
    cursor: pointer;
}
.num2 {
    z-index: 1;
    margin: 0%;
    padding: 10% 4%;
    margin: 0% 0% 0% -33%;
    font-size: 176px;
    height: 10%;
    width: 23%;
    color: #F8E5B5;
}
.two {
    text-align: end;
    width: 82%;
    margin: -1% 0% 0% 2%;
}
.bb1{
    text-decoration: none;
    margin: -6% 6% 0% 25%;
}
.bb2 {
    margin: -4% 6% 0% 6%;
}

.cult-night-reg{
    display: none;
}

.cult-night-exp{
    margin-right: 0%;
}

@media only screen and (min-width: 1500px)  {
    .eone{
        height: 3800px;
    }
}
@media only screen and (min-width: 2000px)  {
    .eone{
        height: 4400px;
    }
}

@media only screen and (max-width: 1305px)  {
    .eone{
        height: 3500px;
    }
.box {
    height: 53px;
}
.first{
    height: 4%;
}
h1{
    line-height: 140px;
    font-size:77px;
}
.two{
    width:84%;
    margin: -1% 0% 0% -6%;
}
.txt{
    height: 6%;
    font-size:30px;
}
.imgg{
    height: 108px;
}
.btnn{
    font-size: 21px;
    margin-right: 22px;
}
.btn2{
     font-size: 21px;
     line-height: 22px;
}

}

@media only screen and (max-width: 1000px)
{
    .txt {
    font-size: 26px;
    line-height: 43px;
}
    h2{
        font-size: 40px;margin-top: 5%;

    }
    .num,.num2{
     margin-top: -2%;
     font-size: 150px;
    }
    .pd{
     width: 30%;
    padding: 6% 6% 3% 5%;
    }
    .btnn{
        font-size: 19px;
        margin-right: 12px;
    }
    .bb1 {
    margin: -6% 6% 0% 12%;
}
    .arrow{
            width: 21%;
    height: 12px;
    }
    .btn1{
        width: 26%;
        padding: 2% 2%;
    }
    .btn2{
        width: 18%;
        font-size: 19px;
    }
    .pd1 {
    width: 26%;
    padding: 6% 15% 3% 4%;
}  .two {
    width: 91%;
    margin: 7% 0% 0% -21%;
}.eone{height: 2900px;}
.first {
    height: 5%;
}
.box1{    margin: 4% 4% 2% 4%;}
}
@media only screen and (max-width: 800px){
h1{
    font-size: 52px;
    margin:0% 4% 0% 1%;
}
.box1{    margin: 5% 4% 2% 4%;}
.txt {
    font-size: 24px;
    line-height: 35px;
}
.num, .num2 {
    font-size: 134px;
}
.btn1{width:30%;}

h2{
    font-size: 35px;
    margin-top: 6%;
}
.btnn{
    font-size: 17px;
    margin-right: 5px; 
}
.btn2 {
    padding: 2% 5%;
    width: 19%;
    font-size: 17px;
    line-height: 20px;
}
.arrow {
    width: 20%;
    height: 12px;
}   
.eone{height: 2700px;
}
.num2{
    margin: 0% 0% 0% -22%;
}
}
@media only screen and (max-width: 625px){
.imgg{
        width: 67%;
    }
.first{
    height: 6%;
}
h1{
        font-size: 45px;
        width: 25%;
    }
.txt{   width: 80%;
        height: 6%;
        font-size: 22px;
        padding-top: 22px;
        line-height: 29px;
        text-align: left;
    }
.eone{
        height: 1900px;
    }
.btn1,.btn2{margin-top: 2%;}
h2 {
    font-size: 30px;
    padding-top: 57px;
}
.num,.num2{font-size: 125px}
.two{
        margin-left:-9%;
    }
.pd1{width: 22%;
    padding-top: 10%;}
.pd{padding: 10% 0% 3% 0%;
    width: 28%;}       
.side1{width: 66%;}
.pd1{padding-right: 4%;}
.btn1 {
    width: 33%;
    line-height: 10px;
}
.btn2{
    line-height: 17px;
    padding: 2% 10%;
    width: 18%;
}
.bb2 {
    margin: 2% 0% 0% 4%;
}
}
@media only screen and (max-width: 450px){
.box{height: 37px;}
h1{line-height: 41px;
   font-size: 35px;}
.imgg{height: 74px;}
.txt{font-size: 18px;
    text-align: left;
     line-height: 20px}
     .two {
    margin-left: -10%;
}

.btnn {
    font-size: 13px;
    line-height: 6px;
}
.arrow{
    width: 19%;
    height: 45%;
}
.btn2{font-size: 13px;
      line-height: 14px;}
h2{
    font-size: 27px;
    padding-top: 46px;
}
.num,.num2{font-size: 100px;}
.eone {
    height: 1600px;
}
}
@media only screen and (max-width: 414px)  {
.eone{
    height: 910px;}
.txt{
    height: 10%;
    font-size: 12px;
}
.box1 {
    margin: 5% 3% 2% 3%;
    width: 44%;
    height: 42%;
}
.pd,.pd1{
    display: none;
}
.two{
    font-size: 18px;
    text-align: start;
    margin-left: 10%;
}
.num2 {
    font-size: 64px;
    margin: 7% 0% 0% -67%;
}
.side1{width: 100%;}
.bb2{display: none;}
.btnn {
    font-size: 14px;
    line-height: 17px;
}
.btn1 {
    width: 60%;
    height: 14%;
    margin-left: 18%;
    border-radius: 3px;
}

}
@media only screen and (max-width: 320px)  {
.arrow{
    display:none;
}
}
