*{
    box-sizing: content-box;
}
.main{
    display: flex;
    justify-content: center;
}
.icons {
    justify-content: center;
    color: white;
    display: flex;
    flex-direction: row;
    width:30%;
    height: 100px;
    align-items: center;
    background-color: white;
}
.namm {
    margin-top: 26%;
    font-size: 35px;
    color: #E7A908;
}

.leftarrow{
    vertical-align: baseline;
}
.icon1{
    background-color: #F0F0F0;
    width: 34px;
    height: 34px;
    border-radius: 31px;
    margin-left: 6%;
}
.icon2 {
    text-decoration: none;
    color: black;
    text-align: center;
    line-height: 47px;
    font-size: 20px;
    background-color: #F0F0F0;
    width: 44px;
    height: 44px;
    border-radius: 44px;
    margin-left: 6%;

}
.icon3 {
    color: white;
    text-decoration: none;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    line-height: 70px;
    text-align: center;
    font-size: 38px;
    background-color: #E7A908;
    width: 68px;
    height: 68px;
    border-radius: 52px;
    margin-left: 6%;
}

.car-control{
    background-color: none !important;
    background-image: none !important;
    width: 8%;
    opacity: 1;
}

.left{
    left: -1.65em;
}

.control-but:before{
    font-size: 40px !important;
    color: white;
    opacity: 1 !important;
    padding: 0.3em;
    border-radius: 200px;
    background-color: #E7A908;
}
.car-indicators{
    bottom: 40px;
}

.icon4{    
    text-decoration: none;
    color: black;
    text-align: center;
    line-height: 47px;
    font-size: 20px;
     background-color: #F0F0F0;
    width: 44px;
    height: 44px;
    border-radius: 44px;
    margin-left: 6%;

}
.icon5{
    text-align: center;
    line-height: 43px;
    background-color: #E7A908;
    width: 34px;
    height: 34px;
    border-radius: 31px;
    margin-left: 6%;
}
.icon6{display: flex;}
.icon7{display: flex;}
.icon7-text, .icon6-text{color:#E7A908;}
.timing{
    justify-content: center;
    height: 260px;
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 3em;
}
.timing-icons{
    margin-top: 15%;
}
.date{
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    background-color: black;
    height: 100%;
    width: 18%;
    border-radius: 15px;
    text-align: center;
    margin: 0% 3%;
}
.nam {
    margin-top: 20px;
    font-size: 33px;
    color: #E7A908;
}
.dat ,.tad {
    margin: -2%;
    line-height: 34px;
    color: white;
    font-size: 22px;
}
.IA {
    margin: 0% 2%;
    width: 40%;
    height: 77%;
    background-color: black;
    border-radius: 5px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.nam2 {
    display: flex;
    flex-direction: row;
    width: 80%;
    margin-bottom: 20px;
}
.dd{
    width: 50%;
}
.dd1{width: 50%;
     margin-left: 5%;}

@media only screen and (max-width: 1000px)  {

.nam {
    font-size: 21px;
}
.control-but:before{
        font-size: 32px !important;
        padding: 0.3em;
}
.left{
   left: -0.3em;
}
.timing{
    height: 200px;
}
.namm {
    margin-top: 35%;
    font-size: 21px;
    color: #E7A908;
}
    .icons{width: 45%;}
    .date{height: 95%;
          width: 21%;
          margin:0% 2%;}
.dat,.tad {
    margin: -6%;
    font-size: 16px;
    line-height: 21px;
}
}
@media only screen and (max-width: 650px){
    .icon1,.icon5{
        height: 20px;
        width: 20px;
        line-height: 25px;
    }
    .icon2,.icon4{
        height: 28px;
        width: 28px;
        font-size: 16px;
        line-height: 30px;
    }
    .control-but:before{
        font-size: 25px !important;
        padding: 0.2em;
    }
    .icon3{
        font-size: 29px;
        height: 44px;
        width: 44px;
        line-height: 47px;
    }
    .leftarrow{
        height: 13px;
    }
    .timing-icons{width: 21%;}
    .nam{font-size: 28px;}
    .spec{
        width: 18%;
        margin-bottom: -5%;
}
    .dat,.tad {
    margin: -3%;
    font-size: 16px;
    line-height: 23px;
}
.timing-icons {
    margin-top: 8%;
}
    .timing{
        height: 156px;
    }
    .nam {
    margin: 6% 10% -22% 10%;
}
.nam3{
    margin: -8% 10% -22% 10%;
}
.nam2 {
    margin: -10% 10% -22% 10%;
}
.nam {
    font-size: 21px;
}
.dat,.tad {
    margin: 9% 1%;
    font-size: 15px;
    line-height: 22px;
}
}
@media only screen and (max-width: 414px){
.timing{height: 285px;
            flex-wrap: wrap;}
.tad {
    color: #E7A908;
    font-size: 14px;
    margin: 8% 7%;
}
.dat {
    color: #E7A908;
    font-size: 14px;
    margin: 21% 7%;
}

.control-but:before{
    font-size: 20px !important;
    padding: 0.3em;
}

.left{
    left: 0em;
}
.date {
    border-radius: 4px;
    width: 40%;
    height: 47%;
}   .timing-icons {
    width: 17%;
}

.nam {
    font-weight: bold;
    font-size: 19px;
}
.icon5{    width: 75px;
    font-weight: 800;
    background: #FFFFFF;

    }
.leftarrow{
    margin-right:5px;
    height: 10px;
}
.icon2,.icon4{
        height: 24px;
        width: 24px;
        font-size: 13px;
        line-height: 26px;
    }
.icon3{
        font-size: 25px;
        height: 40px;
        width: 40px;
        line-height: 42px;
    }
.icons{width: 100%;
       height:70px;
